body {
    background-color: #222831;
}
.loading {
    background-color: #222831;
}
h1 {
    width: 100%;
    height: 70px;
    text-align: center;
    margin: 0px;
    color: #EEEEEE;
    background-color: #FFD369;
    font-size: 50px;
    margin-bottom: 8px;
}
h1 a{
    width: 100%;
    color:#FAF0E6;
}
a {
    text-decoration-line: none;
    color: inherit;
}
h3 {
    margin: 0px;
    margin-top:5px;
    width: 100%;
}
ul {
    margin-bottom: 20px;
    padding-left:  20px;
    padding-bottom: 10px;
}
.box {
    width: 100%;
    height: 100%;

    display: grid;
    grid-template-rows: repeat(auto-fit, 1fr);
    grid-template-columns: repeat(4, 1fr);
    gap: 8px;
    background-color: #222831;
}
.inbox {
    background-color: #393E46;
}

html {
    width: 100%;
    color:#EEEEEE;
}
.inboximg{
    width: 100%;
    height: 60%;
    display: grid;
    grid-template-rows: repeat(auto-fit, 1fr);
    grid-template-columns: repeat(4, 1fr);
    gap: 8px;
}
.textonly {
    margin-left: 5px;
    margin-right: 5px;
}


@media (max-width: 710px) {
    .box {
        width: 100%;
        height: 100%;
    
        display: grid;
        grid-template-rows: repeat(auto-fit, 1fr);
        grid-template-columns: repeat(2, 1fr);
        gap: 8px;
    }
    .inboximg{
        width: 100%;
        height: 50%;
        display: grid;
        grid-template-rows: repeat(auto-fit, 1fr);
        grid-template-columns: repeat(3, 1fr);
        gap: 8px;
    }
}

.DeImg {
    margin-right: 8px;
    width: 25%;
    height: 400px;
    float: right;
}
.DeText {
    position: relative;
    float: left;
    top: 1px;
    left: 8px;
    width: 70%;
    height: 40%;
    padding-left: 10px;
    padding-right: 10px;
    background-color: #393E46;
}
.DeText h2 {
margin-top : 0px;
}

iframe {
    position: absolute;
    width: 25%;
    height: 30%;
    bottom: 45px;
    right: 8px;
    
}

@media (max-width: 1080px) {
    .DeImg {
        margin-right: 8px;
        width: 40%;
        height: 400px;
        float: right;
    }
    .DeText {
        position: relative;
        float: left;
        top: 1px;
        left: 8px;
        width: 54%;
        height: 40%;
        padding-left: 10px;
        padding-right: 10px;
        background-color: #393E46;
    }
    .DeText h2 {
    margin-top : 0px;
    }
    
    iframe {
        position: absolute;
        width: 40%;
        height: 30%;
        bottom: 45px;
        right: 8px;
        
    }
}


@media (max-width: 710px) {
    body {
        background-color:#393E46;
    }
    .DeImg {
    width: 100%;
    margin-right: 0px;
        
    }
    .DeText {
        background-color:#393E46;
        width: 95%;
        padding: 0px;
        margin-bottom: 10px;
        left: 10px;

    }
    h1 {
        margin: 0px;
    }
    iframe {
        position: static;
        padding-left: 0px;
        width: 100%;
        height: 250px;
    }
}